.list-item-filter {
    width: 100%;

    ul {
        margin: 0;
        padding: 0;
        text-align: center;
        border-bottom: 1px solid #d7d7d7;

        li {
            color: #757575;
            padding: 14px 10px;
            list-style: none;
            display: inline-block;
            cursor: pointer;
            font-size: 12px;
            border-radius: 2px;

            &.active {
                color: #353535;
                border-bottom: 2px solid #43BFA4;
            }
        }
    }
}



