// -----------------------------------------------------------------------------
// This file contains all styles related to the button component.
// -----------------------------------------------------------------------------

.button-main {
    display: inline-block;
    width: auto;
    padding: 5px 10px;

    &.button-outline {
        cursor: pointer;
        background: none;
        border: 1px solid #EB5365;
        border-radius: 15px;
        color: #EB5365;
        font-size: 12px;
    }
}

.button-circle {
    display: block;
    padding: 15px 12px;
    cursor: pointer;

    span {
        display: block;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        color: #F5F5F5;
        padding: 1px 6px;
    }

    &.button-add,
    &.button-confirm {
        span {
            background-color: #43BFA4;
        }
    }

    &.button-cancel {
        span {
            background-color: #EB5365;
            transform: rotate(45deg);
        }
    }

}



